import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import SimpleBreadcrumbs from "../../../layout/SimpleBreadcrumbs";
import PrismSyntaxHighlighter from "../../../components/PrismSyntaxHighlighter";
import Hidden from "@material-ui/core/Hidden";
import Particles from "react-particles-js";
import { useTheme } from "@material-ui/core/styles";
import { getHeaderBackgroundColor } from "../../../utils/Theme";
import Footer from "../../../components/Footer";

import {
  useLocation,
  //useParams,
  //useHistory,
  //useRouteMatch,
} from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {
    width: 300,
  },
  container: {
    display: "flex",
    flexDirection: "column",
    [theme.breakpoints.between("xs", "md")]: {
      minHeight: "calc(100vh - 131px)",
    },
    [theme.breakpoints.up("md")]: {
      minHeight: "calc(100vh - 225px)",
    },
  },
}));

export default function Post() {
  const theme = useTheme();
  const location = useLocation();

  const description = location.description || "";
  const pageNumber = location.pageNumber;
  const gistId = location.gistId;
  const classes = useStyles();
  const [rowCount, setRowCount] = useState(0);
  const [content, setContent] = useState("");
  const [language, setLanguage] = useState("Bash");
  //const imageFolder = "/static/img";

  useEffect(() => {
    document.title = "Новости";
    if (!localStorage.getItem(`gist-${gistId}`)) {
      console.log("---load from internet---");
      loadContent();
    } else {
      //console.log("---load from storage---");
      const language = localStorage.getItem(`gist-lang-${gistId}`);
      //console.log("=============");
      //console.log(language);
      setLanguage(language ? language : "lang-js");
      setContent(localStorage.getItem(`gist-${gistId}`));
    }
  }, []);

  useEffect(() => {
    if (content && !localStorage.getItem(`gist-${gistId}`)) {
      //console.log("===save content to storage===");

      localStorage.setItem(`gist-${gistId}`, content);
      localStorage.setItem(`gist-lang-${gistId}`, language);
    }
    //console.log("Content");
    //console.log(content);
  }, [content]);

  const loadContent = () => {
    //console.log("loadContent");
    fetch(`https://api.github.com/gists/${gistId}`)
      .then((res) => res.json())
      .then((data) => {
        //console.log(data);
        const keys = Object.keys(data.files);
        if (keys.length > 0) {
          const first = keys.shift();
          const language = data.files[first].language;
          if (language !== null) {
            setLanguage(language);
          }

          setContent(data.files[first].content);
          const rows = data.files[first].content.split(/\r\n|\r|\n/).length;
          setRowCount(rows);
        }
      });
  };

  const getLang = () => {
    switch (language) {
      case "PHP":
        return "lang-php";
      case "Text":
        return "lang-shell"; //or lang-bash
      case "Bash":
        return "lang-shell";
      case "JavaScript":
        return "lang-js";
      default:
        return `lang-${language.toLowerCase()}`;
    }
  };

  return (
    <React.Fragment>
      <Hidden only={["xs", "sm"]}>
        <Particles
          height={150}
          style={{ backgroundColor: getHeaderBackgroundColor(theme) }}
        />
      </Hidden>
      <Container maxWidth="md" className={classes.container}>
        <SimpleBreadcrumbs
          links={[
            { title: "Главная", link: "/" },
            { title: "Гисты", link: "/gists" },
          ]}
          current={description.slice(0, 30)}
          pageNumber={pageNumber}
        />
        <main>
          <PrismSyntaxHighlighter children={content} className={getLang()} />
        </main>
      </Container>
      <Footer />
    </React.Fragment>
  );
}
